  margin: 0;
  padding: 0;
  border: 1px solid black;
}
html {
  height: 100vh;
}
h1 {
  font-size: 1.5rem;
}
h2 {
  font-size: 1.3rem;
}

h3 {
  font-size: 1.05rem;
}
body {
  margin: 0.5rem;
  height: 100%;
  display: grid;
  grid-template: 1fr 1fr 12fr/1fr 1fr 1fr;
}
header {
  grid-area: 1/1/2/4;
  display: flex;
}

/* unit toggle */
.switch {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 60px;
  height: 34px;
}

#unit-toggle {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  align-self: center;
  vertical-align: middle;
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: whitesmoke;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}

#unit-toggle:checked + .slider {
  background-color: #2196f3;
}

#unit-toggle:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

#unit-toggle:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

#search-form {
  grid-area: 2/1/3/4;

  display: flex;
  height: 85%;
}

input {
  font-size: 1.5rem;
  width: 80%;
  height: 100%;
}

input::placeholder {
  opacity: 0.5;
}
button {
  width: 3rem;
  height: 100%;
  cursor: pointer;
}

#output-overlay {
  max-height: 100%;
  padding: 1rem;
  height: 100%;
  grid-area: 3/1/4/4;
  display: grid;

  grid-template: 0.5fr 2fr 8fr/1fr 1fr 1fr;
}

#output-title {
  width: 80%;
  grid-area: 1 / 1/ 2/4;
}
/* forecast container output */
#current-ctr {
  grid-area: 2/1/3/2;
  display: grid;
  grid-template: 1fr 1fr 1fr/1fr 1fr 1fr;
}
.section-title {
  grid-area: 1/1/2/4;
}

#temp-output {
  grid-area: 2/1/3/2;
}
#description-output {
  grid-area: 3/1/4/2;
}
#feels-like {
  grid-area: 2/3/4/4;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

#current-ctr #icon-ctr {
  grid-area: 2/2/4/3;
}

/*daily forecast styling*/

#daily-forecast {
  grid-area: 2/2/3/4;
}

.daily-list {
  /* height: 100%; */
  display: grid;
  grid-auto-flow: column;
}

#charts-ctr {
  grid-area: 3/1/4/4;
}

.chart-ctr {
  border: none;
  width: 800px;
  display: flex;
}

.colSmall {
  border: none;
  width: 28px;
}

.colLarge {
  border: none;
  max-width: 800px;
  overflow-x: scroll;
}
.box {
  border: none;
  width: calc(2200px - 28px);
  height: 500px;
}
canvas {
  border: none;
}
.icon img {
  height: 3rem;
  width: 3rem;
}
